import { faHeart, faTimes } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import clsx from 'clsx';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import http from '../../../services/http.service';

import './Profile.css';

const Profile = ({ item, likes, dislikes, updateLikes, updateDislikes }) => {
  const [open, setOpen] = useState(false);
  const [currentImage, setCurrentImage] = useState('');
  const [imageList, setImageList] = useState([]);

  useEffect(
    () => {
      const list = [];

      if (item.user.photos.length) {
        setCurrentImage(item.user.photos[0].url);
        list.push(...item.user.photos);
      }

      if (item.instagram && item.instagram.photos) {
        list.push(...item.instagram.photos.map(it => ({ url: it.image })));
      }

      setImageList(list);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const getAge = () => moment().year() - moment(item.user.birth_date).year();

  const onDetail = e => {
    e.preventDefault();
    setOpen(true);
    document.body.classList.add('lock');
  };

  const onClose = e => {
    e.preventDefault();
    setOpen(false);
    document.body.classList.remove('lock');
  };

  const onDislike = async e => {
    e.preventDefault();
    e.stopPropagation();
    
    await http.post('/pass', {
			id: item.user._id,
			s: item.s_number,
		});

    updateDislikes(item.user._id);
  };

  const onLike = async e => {
    e.preventDefault();
    e.stopPropagation();

		await http.post('/like', {
			id: item.user._id,
			s: item.s_number,
		});

    updateLikes(item.user._id);
	};

  const changePreview = (url, e) => {
    e.preventDefault();
    setCurrentImage(url);
  };

  const isLike = likes.indexOf(item.user._id) !== -1;
  const isDislike = dislikes.indexOf(item.user._id) !== -1;

  return (
    <>
      <div className="profile" onClick={onDetail}>
        <img alt="" src={item.user.photos[0].url} />
        <div className="info">
          <span className="name">{item.user.name}, {getAge()}</span>
          <div className="actions">
            <button className={clsx('dislike', { hidden: isLike })} onClick={onDislike} disabled={isDislike}>
              <FontAwesomeIcon icon={faTimes} />
            </button>
            <button className={clsx('like', { hidden: isDislike })} onClick={onLike} disabled={isLike}>
              <FontAwesomeIcon icon={faHeart} />
            </button>
          </div>
        </div>
      </div>
      {open && (
        <div className="full-profile">
          <div className="preview">
            <img alt="" src={currentImage} />
          </div>
          <div className="info">
            <button className="close" onClick={onClose}>
              <FontAwesomeIcon icon={faTimes} />
            </button>
            <h1>{item.user.name}, {getAge()}</h1>
            <span className="distance">{(item.distance_mi * 1.6).toFixed(1)} km</span>
            <p>
              {item.user.bio}
            </p>
            <div className="gallery">
              {imageList.map((it, index) => (
                <img
                  alt=""
                  key={index}
                  src={it.url}
                  className={clsx({ active: it.url === currentImage })}
                  onClick={changePreview.bind(null, it.url)}
                />
              ))}
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Profile;
