import React, { useEffect, useRef, useState } from 'react';
import http from '../../../services/http.service';
import Profile from '../../components/profile/Profile';

import './Dashboard.css';

const Dashboard = () => {
	const [data, setData] = useState([]);
	const [dups, setDups] = useState(0);

	const [likes, setLikes] = useState([]);
	const [dislikes, setDislikes] = useState([]);

	const loading = useRef(false);
	const ids = useRef([]);
	const removerLikes = useRef();
	const removerDislikes = useRef();

	useEffect(
		() => {
			load();
			window.addEventListener('scroll', onScroll, false);
			return () => window.removeEventListener('scroll', onScroll);
		},
		// eslint-disable-next-line react-hooks/exhaustive-deps
		[]
	);

	useEffect(
		() => {
			loading.current = false;
		},
		[data]
	);

	const onScroll = e => {
		const elm = document.scrollingElement;
		if (!loading.current && elm.scrollTop > (elm.scrollHeight - window.innerHeight - 250)) {
			load();
		}
	};

	const load = async () => {
		loading.current = true;
		try {
			const res = await http.get('/').then(res => res.data);
			const filtered = res.length ? res.filter(it => {
				const result = ids.current.indexOf(it.user._id) === -1;
				if (result) {
					ids.current.push(it.user._id);
				}
				return result;
			}) : [];

			setDups(p => p + (res.length - filtered.length));

			if (filtered.length) {
				setData(p => [...p, ...filtered]);
			} else {
				load();
			}
		} catch (e) {
			window.localStorage.removeItem('tinderToken');
			window.location.reload();
		}
	};

	const negateAll = async e => {
		e.preventDefault();
		const currentData = [...data];

		const rem = async index => {
			setData(p => p.filter(it => it.user._id !== currentData[index].user._id));
			if (likes.indexOf(currentData[index].user._id) === -1) {
				await http.post('/pass', {
					id: currentData[index].user._id,
					s: currentData[index].s_number,
				}).then(res => res.data);
				if (index < currentData.length - 1) {
					rem(index + 1);
				} else {
					load();
				}
			}
		};

		if (currentData.length) {
			rem(0);
		}
	};

	const updateLikes = userId => {
		setLikes(p => [...p, userId]);
		if (removerLikes.current.checked) {
			setData(p => p.filter(it => it.user._id !== userId));
		}
	};

	const updateDislikes = userId => {
		setDislikes(p => [...p, userId]);
		if (removerDislikes.current.checked) {
			setData(p => p.filter(it => it.user._id !== userId));
		}
	};

	return (
		<div className="dashboard">
			<header>
				<button onClick={negateAll} className="negate-all">Negar Todos</button>
				<div className="removerLikes">
					<input type="checkbox" name="remover-likes" id="remover-likes" ref={removerLikes} />
					<label htmlFor="remover-likes">Remover após o like</label>
				</div>
				<div className="removerDislikes">
					<input type="checkbox" name="remover-dislikes" id="remover-dislikes" ref={removerDislikes} />
					<label htmlFor="remover-dislikes">Remover após o dislike</label>
				</div>
				<span className="counter">{data.length}/{dups}</span>
			</header>
			<div className="list">
				{data.map(it => (
					<Profile
						key={it.user._id}
						item={it}
						likes={likes}
						dislikes={dislikes}
						updateLikes={updateLikes}
						updateDislikes={updateDislikes}
					/>
				))}
			</div>
		</div>
	);
}

export default Dashboard;
