import React, { useState } from 'react';

import './Token.css';

const Token = () => {
	const [token, setToken] = useState('');	

	const onChange = e => setToken(e.target.value);

	const onClick = e => {
		e.preventDefault();
		if (token) {
			window.localStorage.setItem('tinderToken', token);
			window.location.reload();
		}
	};

	return (
		<div className="token-page">
			<input type="text" name="token" placeholder="Token" onChange={onChange} />
			<button onClick={onClick}>Entrar</button>
		</div>
	);
};

export default Token;
