import axios from 'axios';

const getDefaultTransformers = () =>
	Array.isArray(axios.defaults.transformRequest) ? axios.defaults.transformRequest : [axios.defaults.transformRequest];

const http = axios.create({
	baseURL: 'http://cast.celiorodrigues.com:6380',
	transformRequest: [
		(data, header) => {
			const token = window.localStorage.getItem('tinderToken');
			if (token) {
				header['tinder-token'] = token;
			}
			return data;
		},
		...getDefaultTransformers(),
	]
});

export default http;
